@media screen and (min-width: 50px) {
  .wrap {
    max-width: 320px;
  }

  .header {
    position: fixed;
    display: none;
    font-size: 0;
    z-index: 50000;
    min-width: 100%;
    background-color: #ffffff;
    box-shadow: 0 12px 30px -10px rgba(0, 0, 0, 0.2);
    opacity: 1;
    transition: min-height ease 0.5s, opacity ease 0.5s;
  }

  .headerBlock {
    display: flex;
    justify-content: space-between;
    padding-top: 7px;
    padding-left: 15px;
    padding-right: 15px;
    padding-bottom: 10px;
    background-color: #fff;
  }

  .headerBlock__img img {
    max-width: 61%;
  }

  .headerBlock__text {
    display: none;
  }

  .headerBlock__adr {
    display: none;
  }

  .btnOval {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 129px;
    height: 37px;
    margin-top: 5px;
    font-family: 'Segoe UI', sans-serif;
    font-weight: normal;
    font-style: normal;
    color: #ffffff;
    font-size: 12.2px;
    text-transform: uppercase;
    border-radius: 25px;
    border: 3px solid #fff;
    background-color: #b7db2a;
    cursor: pointer;
  }

  .topSection {
    min-height: 367px;
  }

  .topSectionBlock {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 20px;
  }

  .topAdrBlock {
    display: flex;
    flex-direction: column;
    align-items: center;
    order: 1;
    font-family: 'Segoe UI', sans-serif;
    font-weight: normal;
    font-style: normal;
    font-size: 14px;
    margin-top: 16px;
  }

  .topAdrBlock a {
    color: #ffffff;
  }

  .topTextImgBlock {
    display: flex;
  }

  .topTextImgBlock__text {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 290px;
    font-family: 'ubuntumedium', sans-serif;
    font-weight: normal;
    font-style: normal;
    font-size: 36px;
    color: #ffffff;
    padding-top: 17px;
  }

  .topTextImgBlock__text p {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 100%;
  }

  .topTextImgBlock__text p span {
    display: block;
    margin-top: -6px;
  }

  .topTextImgBlock__text p:last-child {
    text-align: center;
    max-width: 288px;
    font-size: 16px;
    margin-top: 11px;
  }

  .topTextImgBlock__img {
    display: none;
  }

  .topSectionBlock__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: 'Segoe UI', sans-serif;
    font-weight: normal;
    font-style: normal;
    color: #ffffff;
    text-transform: uppercase;
    border-radius: 25px;
    border: 3px solid #fff;
    background-color: #b7db2a;
    font-size: 18px;
    margin-left: auto;
    margin-right: auto;
    position: absolute;
    width: 175px;
    height: 46px;
    bottom: -68px;
    left: 0;
    right: 0;
    margin: 0 auto;
    cursor: pointer;
  }

  .orderInstSection {
    position: relative;
    background-color: #f6fdff;
  }

  .orderInstBlock {
    padding-top: 48px;
    line-height: 1.2;
  }

  .order {
    font-family: 'Segoe UI', sans-serif;
    font-weight: normal;
    font-style: normal;
    font-size: 18px;
    color: #363636;
    text-align: center;
  }

  .order p {
    margin-bottom: 20px;
  }

  .order b {
    font-family: 'Segoe UI', sans-serif;
    font-weight: bold;
    font-style: normal;
    color: #00b9ee;
  }

  .order:last-child {
    padding-top: 20px;
  }

  .order:last-child p {
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    width: 200px;
    line-height: 1.3;
    margin-bottom: 17px;
  }

  .order:last-child p span {
    position: relative;
    display: inline-block;
    font-size: 0;
    width: 100px;
    height: 27px;
  }

  .order:last-child p span img {
    position: absolute;
    bottom: -5px;
    left: 2px;
  }

  .order__btn {
    font-size: 18px;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 160px;
    height: 40px;
    text-transform: uppercase;
    color: #ffffff;
    border-radius: 27px;
    border: none;
    background-color: #f39200;
    cursor: pointer;
  }

  .order__btn img {
    display: none;
  }

  .order__btn--green {
    background-color: #b7db2a;
  }
}

@media screen and (min-width: 480px) {
