    left: 0;
    right: 0;
  }

  .order {
    font-size: 24px;
  }

  .order p {
    margin-bottom: 23px;
  }

  .order:last-child {
    padding-top: 0;
    margin-top: -5px;
    margin-left: 115px;
  }

  .order:last-child p {
    width: 260px;
  }

  .order:last-child p span {
    width: 129px;
  }

  .order__btn {
    width: 190px;
    height: 45px;
    position: relative;
  }

  .order__btn img {
    display: block;
    max-width: 33%;
    position: absolute;
    bottom: -9px;
    right: -24px;
  }
}

@media screen and (min-width: 920px) {
