/*Общее для сайта*/
.wrap{
    .from(50px,{
        max-width: 320px;
    });
    .from(750px,{
        max-width: 750px;
    });
    .from(1170px,{
        max-width: 1170px;
    });
}

.header{
    .from(50px,{
        position: fixed;
        display: none;
        font-size: 0;

        z-index: 50000;

        min-width: 100%;
        background-color: @white;
        box-shadow: 0 12px 30px -10px rgba(0,0,0,0.2);

        opacity: 1;
        transition: min-height ease .5s, opacity ease .5s;
    });
    .from(756px,{
        display: block;
    });
    .from(1170px,{
        display: none;
    });
}

.header.active{
    display: block;
    opacity: 1;
}

.headerBlock{
    .from(50px,{
        display: flex;
        justify-content: space-between;

        padding-top: 7px;
        padding-left: 15px;
        padding-right: 15px;
        padding-bottom: 10px;

        background-color: #fff;
    });
    .from(756px,{
        justify-content: center;
    });
    .from(1170px,{
        justify-content: space-between;
        padding-bottom: 19px;
        padding-right: 5px;
        padding-left:5px;
    });
}

.headerBlock__img{
    display: block;
    img{
        .from(50px,{
            max-width: 61%;
        });
        .from(756px,{
            max-width: 25%;
        });
        .from(1170px,{
            max-width: 100%;
        });
    }

    .from(756px,{
        flex-grow: 1;
    });
    .from(1170px,{
        flex-grow: 0;
        margin-left: 10px;
    });
}

.headerBlock__text{
    .from(50px,{
        display: none;
    });
    .from(756px,{
        display: block;
        position: relative;

        .ubuntumedium();
        font-size: 20.62px;

        width: 225px;

        margin-top: 12px;
        margin-right: 7px;
    });
    .from(1170px,{
        font-size: 33.8px;
        margin-top: 19px;
        width: 365px;
        margin-right: 0;
        margin-left: 74px;
    });
    span{
        .from(756px,{
            display: inline-block;
            width: 107px;
            height: 27px;

            position: absolute;
            top: -2px;
            right: 0;
        });
        .from(1170px,{
            width: 172px;
            height: 47px;
        });
    }
}

.headerBlock__adr{
    .from(50px,{
        display: none;
    });
    .from(1170px,{
        display: block;

        margin-top: 11px;

        .SegoeUIReg();

        p{

            color: #00b9ee;
            font-size: 16px;

            &:first-child{
                color: #3a3a3a;
                font-size: 14px;
            }

            a{
                color: #3a3a3a;
            }
        }
    })
}

.btnOval{
    .from(50px,{
        display: flex;
        align-items: center;
        justify-content: center;

        width: 129px;
        height: 37px;

        margin-top: 5px;

        .SegoeUIReg();
        color: @white;
        font-size: 12.2px;

        text-transform: uppercase;

        border-radius: 25px;
        border: 3px solid #fff;
        background-color: #b7db2a;

        cursor: pointer;
    });
    .from(1170px,{
        width: 205px;
        height: 56px;
        font-size: 20px;
        margin-top: 11px;
        border-radius: 27px;
        margin-right: 60px;
    });
}

